import React, { useEffect } from 'react';
import FiltersSituationsListItem from './FiltersSituationsListItem';
import useScrollHorizontally from 'hooks/useScrollHorizontally';
import cl from './FiltersSituationsList.module.scss';

const FiltersSituationsList = ({ situationFilter, situations, handleClick, setIsLeftScroll, setIsRightScroll }) => {
  const [scrollRef, scrollLeft, scrollRight, addListeners, removeListeners, scrollFixation] =
    useScrollHorizontally();

  useEffect(() => {
      scrollFixation();
      addListeners();
    return () => {
      removeListeners();
    };
    // eslint-disable-next-line
  }, []);

	useEffect(() => {
		setIsLeftScroll(scrollLeft)
		setIsRightScroll(scrollRight)
	}, [scrollLeft, scrollRight])

  console.log(scrollLeft);
  console.log(scrollRight);

  return (
    <ul ref={scrollRef} className={cl.situationsList}>
      {situations.map((situation, i) => (
        <FiltersSituationsListItem
          key={i}
          situationFilter={situationFilter}
          situation={situation}
          handleClick={handleClick}
          cl={cl}
        />
      ))}
    </ul>
  );
};

export default FiltersSituationsList;
